<template>
  <div id="app">
    <v-app footer light>
      <header id="site-header">
        <v-layout row wrap align-end justify-center fill-height>
          <v-flex xs11 sm10 md9 class="text-xs-center">
            <div class="display-2">Adrian Cerbaro</div>
            <div class="subheading">Front and Back End Developer</div>
          </v-flex>
          <v-flex xs12>
            <v-toolbar color="transparent" dense flat dark>
              <v-toolbar-title class="hidden-xs-only">
                <v-avatar :tile="true" size="32px"><img src="/static/img/logo-white-alpha.png" alt=""></v-avatar>
              </v-toolbar-title>
              <v-spacer></v-spacer>
              <v-toolbar-items>
                <v-btn v-for="(btn, i) in nav" :key="i" flat :to="btn.path">
                  <v-icon v-if="btn.icon" left>{{ btn.icon }}</v-icon>
                  {{ btn.title }}
                </v-btn>
              </v-toolbar-items>
            </v-toolbar>
          </v-flex>
        </v-layout>
      </header>

      <v-content>
        <v-container fluid class="pa-0">
          <transition name="slide-x-transition" mode="out-in">
            <router-view></router-view>
          </transition>
        </v-container>
      </v-content>

      <v-footer class="px-3">
        <v-spacer></v-spacer>
        <small>Hosted by <a href="https://github.com/">GitHub</a></small>
      </v-footer>
    </v-app>
  </div>
</template>

<script>
export default {
  name: 'App',
  data () {
    return {
      nav: [
        {
          title: 'Início',
          path: '/',
          icon: 'home'
        }
      ]
    }
  }
}
</script>

<style lang="stylus">
@import 'assets/stylus/main';
@import '~vuetify/src/stylus/main'
</style>



