<template>
  <v-container id="portfolio" class="portfolio">
    <h1 class="page-title">Portfolio</h1>

    <v-layout row wrap>
      <template v-for="(item, i) in list">
        <v-flex xs12 sm6 md4 v-bind:key="i">
          <v-card class="portfolio__item">
            <v-card-media :src="`/static/img/portfolio/${item.name}/card.png`" height="200px">
              <v-container fill-height fluid>
                <v-layout fill-height>
                  <v-flex xs12 align-end d-flex>
                    <span class="headline white--text" v-text="item.title"/>
                  </v-flex>
                </v-layout>
              </v-container>
            </v-card-media>
            <v-card-title primary-title>
              <div>
                <div v-html="item.content"></div>
              </div>
            </v-card-title>
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn flat color="teal" target="_blank" tag="a" :href="item.url">
                <v-icon left>open_in_new</v-icon>
                Visitar
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-flex>
      </template>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  name: 'Portfolio',
  data () {
    return {
      list: [
        {
          name: 'cerbaroestof',
          title: 'Cerbaro Estofamentos',
          content: 'Um site <a href="https://wordpress.org" target="_blank" title="WordPress">WordPress</a> baseado no <a href="https://material.io/" target="_blank" title="Material Design">Material Design</a>, feito para a empresa <strong>Cerbaro Estofamentos</strong>. Totalmente responsivo e com várias funcionalidades de customização.',
          url: 'https://cerbaroestof.com.br/'
        }
      ]
    }
  }
}
</script>



