import Vue from 'vue'
import Router from 'vue-router'
// eslint-disable-next-line
import Home from '@/pages/Home'
import Portfolio from '@/pages/Portfolio'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Home',
      components: {
        default: Portfolio, // Home
        portfolio: Portfolio
      }
    },
    {
      path: '/portfolio',
      name: 'Portfolio',
      component: Portfolio
    }
  ]
})



